.no-tailwindcss-base h1, .no-tailwindcss-base h2, .no-tailwindcss-base h3, .no-tailwindcss-base h4, .no-tailwindcss-base h5, .no-tailwindcss-base h6, .no-tailwindcss-base b, .no-tailwindcss-base strong { font-weight: bolder; }
